import React from 'react';


const Tools = () => (
    <div style={{textAlign:'center'}} className="tool-box" id="tools">
        <div className="container">
            <h1>#Toolbox</h1>
            <div className="toolbox-container" style={{textAlign: 'left'}}>
                <p><span className="tool">Languages:</span> HTML5, CSS3/SCSS, JavaScript, PHP, Python</p>
                <p><span className="tool">Frameworks:</span> React, React Native, Node, Angular 5</p>
                <p><span className="tool">Databases:</span> MongoDB, MySQL</p>
                <p><span className="tool">Other:</span> Git, MAMP, WordPress, Grunt, Gulp, Webpack, Babel, Bootstrap, Zurb Foundation, Materialize, Bash/CLI, Heroku, AWS</p>
            </div>
        </div>
    </div>
)


export default Tools;


