import React from 'react';

class WhatIDo extends React.Component{
    state = {
        wordpress: false
    }
    handleClick = () => {
        this.setState({wordpress: !this.state.wordpress});
    }
    render(){
        return(
            <section id="what-i-do">
                    <div className="container">
                        <div className="row">
                            <div className="col-sm-6 wow fadeIn" data-wow-delay="1s"><img src={require('../img/imac.svg')} id="imac-img" alt="imac with keyboard and mouse and code on the screen"/> </div>
                            <div className="col-sm-6">
                                <h1 className="h1-intro wow bounceIn">When WordPress Isn't Enough</h1>
                                <p>Web development is more than just designing and creating a website to display information. While sites like WordPress and 
                                    Squarespace are great in providing consumers with basic customization options,
                                    custom features/design will require programming knowledge.
                                    Additionally, if your business is targeting a specific demographic, you won't be able to strategically develop your site for the most effective user experience.
                                </p>
                            </div>
                        </div>
                        <div className="centered help-container">
                            <h1 className="wow fadeIn" data-wow-delay='1.4s'>Here's How I Can Help</h1>                           
                            {this.state.wordpress ? <WordPress />: <NoWordPress />} 
                            <a className="btn custom-btn" style={{color: 'white'}} onClick={this.handleClick}>{this.state.wordpress ? 'End To End Business Solutions' : 'WordPress Solutions'}</a> 
                        </div>
                    </div>
                </section>
        )
    }
} 

const WordPress = () => (
    <div>
    <p className="tool wow fadeInUp" data-wow-delay='0.5s' style={{textAlign: 'center', marginTop: '2%'}}>If your business prefers WordPress solutions: </p>
        <div className="row">
            <div className="col-sm-4 wow fadeIn" data-wow-delay="0.2s">
                <img src={require('../img/wordpress1.svg')} style={{width: '100%'}} alt='custom wordpress themes'/>
            </div>
            <div className="col-sm-4 wow fadeIn" data-wow-delay="0.4s" alt='custom wordpress plugins'>
            <img src={require('../img/wordpress2.svg')} style={{width: '100%'}} alt="custom wordpress plugins"/>
            </div>
            <div className="col-sm-4 wow fadeIn" data-wow-delay="0.6s" >
                <img src={require('../img/wordpress3.svg')} style={{width: '100%'}} alt='implement advanced plugins'/>
            </div>
        </div>
    </div>
)
const NoWordPress = ()=> (
        <div style={{margin: '3% auto'}}>
            <p className="tool wow fadeInUp" data-wow-delay='0.5s' style={{textAlign: 'center'}}>What I can do for your business: </p>
            <div className="row" style={{marginTop: '5%'}}>
                <div className="col-sm-4 wow fadeIn" data-wow-delay="0.7s">
                    <img src={require('../img/wand.svg')} style={{width: '100%'}} alt='design'/>
                    <p className="tool" style={{marginTop: '3%'}}>
                    Custom Digital Artwork <br/>
                    Wireframes <br/>
                    Prototypes <br/>
                    Photo Manipulation
                    </p>
                </div>
                <div className="col-sm-4 wow fadeIn" data-wow-delay="0.9s" >
                <img src={require('../img/code.svg')} style={{width: '100%'}} alt='code'/>
                <p className="tool" style={{marginTop: '3%'}}>
                    Full Stack Web Apps <br/>
                    Incorporate API<br/>
                    Landing Pages <br/>
                    Mobile Apps
                    </p>
                </div>
                <div className="col-sm-4 wow fadeIn" data-wow-delay="1.1s" >
                    <img src={require('../img/charts.svg')} style={{width: '100%'}} alt='marketing'/>
                    <p className="tool" style={{marginTop: '3%'}}>
                    Google PPC Ads <br/>
                    Google Analytics <br/>
                    Website SEO <br/>
                    HTML Marketing Emails
                    </p>
                   
                </div>
            </div>
        </div>
)

export default WhatIDo;


