import React from 'react';

const Footer = () => (
    <section id="footer">
      <div className="container">
        <div className="row media-icons">
            <div className="col-3">
                <a href="https://facebook.com/april.del.7" target="_blank" rel="noopener noreferrer"><img src={require('../img/facebook.png')} alt="facebook icon" /></a>
            </div>
            <div className="col-3">
                <a href="https://twitter.com/kayleos" target="_blank" rel="noopener noreferrer"><img src={require('../img/twitter.png')} alt="twitter icon"/></a>
            </div>
            <div className="col-3">
                <a href="https://instagram.com/kayleoss" target="_blank" rel="noopener noreferrer"><img src={require('../img/instagram.png')} alt="instagram icon"/></a>
            </div>
            <div className="col-3">
                <a href="https://github.com/kayleoss" target="_blank" rel="noopener noreferrer"><img src={require('../img/github.png')} alt="github icon"/></a>
            </div>
        </div> 
      </div>
      <div className="copyright">
        Built With  by Katie Liu &copy; All rights reserved 2018       
      </div>
    </section>
)

export default Footer;


