import React from 'react';
import Apps from './Apps';
import Websites from './Websites';
import Games from './Games';

class Projects extends React.Component{
    state = {
        projects: 'websites',
        website: true,
        apps: false,
        games: false
    }
    handleClickWebsites = () => {
        this.setState({projects: 'websites', website: true, apps: false, games: false})
    }
    handleClickApps = () => {
        this.setState({projects: 'apps', website: false, apps: true, games: false})
    }
    handleClickGames = () => {
        this.setState({projects: 'games', games: true, website: false, apps: false})
    }
    render(){
        var projects;
        if(this.state.projects === 'apps'){
            projects = <Apps />
        }else if(this.state.projects === 'websites'){
                projects = <Websites />
        }else if(this.state.projects === 'games'){
            projects = <Games />
        }

        return(
            <section id="#projects">
                <div className="container">
                    <div className="row">
                        <div className="col-sm-6">
                            <img src={require('../img/project.svg')} id="project-img" alt=""/>
                        </div>
                        <div className="col-sm-6">
                            <h1 className="h1-intro wow bounceIn">My Recent Projects</h1>
                            <p className="description">Below you'll find some of my most recent projects, either created for a business or for myself. 
                            All of my projects' code can be found on my <a href="https://github.com/kayleoss" target="_blank" rel="noopener noreferrer">Github profile</a>.
                             <span className="emphasize"> Please note that some recent projects are not listed because they belong to the employer*</span>. Additionally, 
                            real data on projects may be replaced with dummy data.
                            Please also allow at least 10 seconds for Node apps as they need to wake from sleep for the demo.
                            </p>
                        </div>
                    </div>
                    <div className="project-nav">
                        <button onClick={this.handleClickWebsites} className="btn custom-btn2" style={{background: this.state.website ? 'lightpink' : 'rgb(198, 247, 240)'}}>Websites/Landing Pages</button>
                        <button onClick={this.handleClickApps} className="btn custom-btn2" style={{background: this.state.apps ? 'lightpink' : 'rgb(198, 247, 240)'}}>Web Apps</button>
                        <button onClick={this.handleClickGames} className="btn custom-btn2" style={{background: this.state.games ? 'lightpink' : 'rgb(198, 247, 240)'}}>Web Games</button>
                    </div>
                    {projects}
                </div>
            </section>
        )
    }
}
export default Projects;


