import React from 'react';

class ChangeTheWorld extends React.Component{
    render(){
       return(
            <div style={{textAlign:'center'}} id="change-the-world">
            <div id="scroll-down" className="wow fadeIn" data-wow-delay="1.5s">
                <img src={require('../img/scroll_gif.gif')} alt="Scroll down the page!"/>
            </div>
            <h1 id="h1-head">#ChangeTheWorld</h1>
            <p id="marketing-line">My dream is to build products that change the way we live. The tech industry is growing at an 
            alarming rate and there is no excuse not to get involved. It only takes a small team to create something big, so I believe the right 
            time to start is <span className="emphasize">right now</span>! Here's my main stack: 
            </p>
            <div className="container">
                <div className="row technology-icons">
                    <div className="col-sm-3 wow fadeIn" data-wow-delay="1.2s"><img src="https://www.damiencosset.com/wp-content/uploads/2017/07/mongodb-1.png" alt="mongodb logo" /></div>
                    <div className="col-sm-3 wow fadeIn" data-wow-delay="1.4s"><img src="https://vladmrnv.gallerycdn.vsassets.io/extensions/vladmrnv/expresssnippet/0.2.3/1515567688651/Microsoft.VisualStudio.Services.Icons.Default" alt="express logo"/></div>
                    <div className="col-sm-3 wow fadeIn" data-wow-delay="1.6s"><img src="https://cdn.worldvectorlogo.com/logos/react.svg" alt="react logo"/></div>
                    <div className="col-sm-3 wow fadeIn" data-wow-delay="1.8s"><img src="http://endeavor.in/sites/default/files/nodejs-logo_0.png" alt="nodeJS logo"/></div>
                </div>
            </div>
            </div>
       )
   } 
} 
    


export default ChangeTheWorld;


