import React from 'react';
import {Link} from 'react-router-dom';
import ChangeTheWorld from './ChangeTheWorld';
import Toolbox from './Tools';

class Home extends React.Component{
    state = {
        toolbox: false,
        button: false,
    }
    handleClick = () => {
        this.setState({toolbox: !this.state.toolbox, button: !this.state.button});
    }
    render(){
        return(
            <div id="home">
            <div className="container">
            <div className="row">
                <div className="col-sm-6">
                    <img src={require('../img/comic.png')} alt="Katie Liu wearing a knit sweater." className="wow fadeIn" data-wow-delay="1s" id="face-photo"/>
                </div>
                <div className="col-sm-6">
                    <h1 className="wow fadeIn h1-intro">Katie Liu</h1>
                    <p className="media-show-line">Software Developer building Web &amp; Mobile apps, Wordpress Themes, Landing Pages, &amp; More.</p>
                    <p className="description wow fadeIn" data-wow-delay="1s">
                        My name is Katie. I'm a <span className="emphasize">full stack</span> software engineer building web applications, web games, landing pages, HTML marketing emails, mobile apps, and more. 
                        Ever wanted to <span className="border-bottom">turn your idea into a reality</span>? I'm here to help! 
                        I've got chops in all stages of the product: Research &amp; Planning, UI/UX Design, Prototyping, Testing, Development, Marketing &amp; SEO, and more! 
                    </p>
                    <Link to='/what-i-do' className="btn custom-btn wow bounceIn" data-wow-delay="1.5s">What I Can Do For You</Link>
                </div>
            </div>
                {this.state.toolbox ? <Toolbox /> : <ChangeTheWorld />}
                    <div style={{textAlign: 'center'}}>
                        <a className="btn custom-btn wow fadeInUp" data-wow-delay="1.8s" onClick={this.handleClick} style={{color: 'white'}}>{this.state.button ? 'See My Goals' : 'See All My Skills'}</a>
                    </div>
                </div>
            </div>
        )
    }

}

export default Home;


