import React from 'react';


const GetStarted = () => (
    <section id="getstarted">
        <div className="container">
            <div className="row getstarted">
                <div className="col-sm-6">
                    <img src={require('../img/envelope.png')} id="envelope-img" alt="envelope" />
                </div>
                <div className="col-sm-6">
                    <h1 className="h1-intro">Get Started</h1>
                    <p className="description">Are you ready to work together? Fill out the form details below 
                        to get in touch with me directly and start working on some amazing 
                        cutting edge projects! </p>
                </div>
            </div>
            
            <form action="https://formspree.io/katie_acting@live.ca" method="post" className="get-started-form">
            <h1 style={{textAlign:'center'}}>Let's Work Together!</h1>
                <div className="container">
                <div className="row">
                    <div className="col-sm-6">
                        <input type="text" name="firstName" placeholder="Your First Name" required/>
                    </div>
                    <div className="col-sm-6">
                        <input type="text" name="lastName" placeholder="Your Last Name" required/>
                    </div>
                </div>
                <input type="text" name="company" placeholder="Name Of Your Company (optional)"/>
                <input type="email" name="email" placeholder="Your Email Address" required/>
                <textarea name="idea" placeholder="Tell Me About Your Idea..."></textarea>
                <div style={{textAlign:'center',marginTop:'3%'}}>
                    <button type="submit" className="btn custom-btn">Submit</button>
                </div>
                </div>
            </form>
           
        </div>
    </section>
)

export default GetStarted;


